import * as React from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import { CardActionArea, Grid } from '@mui/material';


export default function ActionAreaCard() {
const go=()=>{window.location.href ="https://muhammedrameesz.github.io/Chessboard/"}
const goo=()=>{window.location.href ="https://muhammedrameesz.github.io/Bulb-on-off/"}
const fb=()=>{window.location.href ="https://muhammedrameesz.github.io/Face-book/"}
const tour=()=>{window.location.href ="https://github.com/Muhammedrameesz/Kerala-tourism.git"}

  return (
    <>
  <Grid container spacing={3} fontFamily={"Courier New"}>

<Grid item md={2.4} xs={3}>

    <Card onClick={tour}  sx={{ 
        maxWidth: 345,
        marginTop: 10,
       height:200,
       ':hover':{
        // cursor: 'pointer',
        // borderRadius: '30%',
        //  transition:'all 1s ease-in-out',
        //  transform: 'translateX(10px)',
        cursor: 'pointer',
        transition:'all 0.30s ',
        transform:'translatey(-10px)',
        boxShadow: '5px 5px 5px 5px ',
       }
        
    
    }}>
      <CardActionArea  >
        <CardMedia
         component='img'
         height='140'
        image='https://static.wixstatic.com/media/c8e24e_8734146da4f241b591e5f0ede582f9e0~mv2.jpg/v1/fill/w_1349,h_558,al_c,q_85,usm_0.66_1.00_0.01,enc_auto/c8e24e_8734146da4f241b591e5f0ede582f9e0~mv2.jpg' alt='img'
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="div">
            Kerala Tourism 
          </Typography>
          <Typography variant="body2" color="text.secondary">
           
          </Typography>
        </CardContent>
      </CardActionArea>
    </Card>
    </Grid>

     {/* Second  */}
     <Grid item md={2.4} xs={3}>

    <Card onClick={go}  sx={{ 
        maxWidth: 345,
        marginTop: 10,
        height:200,
       
        ':hover':{
          // cursor: 'pointer',
          // borderRadius: '30%',
          //  transition:'all 1s ',
          // transform: 'translateX(10px)',
          cursor: 'pointer',
          transition:'all 0.30s ',
          transform:'translatey(-10px)',
          boxShadow: '5px 5px 5px 5px ',

        }
    
    }}>
      <CardActionArea  >
        <CardMedia
         component='img'
         height='140'
        image='data:image/jpeg;base64,'
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="div">
            Chess Board
          </Typography>
          <Typography variant="body2" color="text.secondary">
           
          </Typography>
        </CardContent>
      </CardActionArea>
    </Card>
    </Grid>

{/* third card  */}
<Grid item md={2.4} xs={3}>

    <Card onClick={fb}  sx={{ 
        maxWidth: 345,
        marginTop: 10,
        height:200,
       boxShadow: '100px',
        ':hover':{
        //   cursor: 'pointer',
        //   borderRadius: '30%',
        //    transition:'all 1s ease-in-out',
        //  transform: 'translateX(10px)',
        cursor: 'pointer',
           transition:'all 0.30s ',
           transform:'translatey(-10px)',
           boxShadow: '5px 5px 5px 5px ',
          

        }
    }}>
      <CardActionArea  >
        <CardMedia
         component='img'
         height='140'
        image='data:image/jpeg;base64,'
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="div">
            Face Book 
          </Typography>
          <Typography variant="body2" color="text.secondary">
            
          </Typography>
        </CardContent>
      </CardActionArea>
    </Card>
    </Grid>

{/* fourth card  */}
<Grid item md={2.4} xs={3}> 

    <Card onClick={goo}  sx={{ 
        maxWidth: 345,
        marginTop: 10,
        height:200,
    
        ':hover':{
          cursor: 'pointer',
           transition:'all 0.30s ',
           transform:'translatey(-10px)',
           boxShadow: '5px 5px 5px 5px ',
          backdropFilter:'blur',
        }
    }}>
      <CardActionArea  >
        <CardMedia
         component='img'
         height='140'
        image='data:image/jpeg;base64,'
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="div">
            Bulb On-Off
          </Typography>
          <Typography variant="body2" color="text.secondary">
            
          </Typography>
        </CardContent>
      </CardActionArea>
    </Card>
    </Grid>

    </Grid>



    </>
  );
}